          //     if (res.rows[i].planQty) {
          //       // oeeArr.push((Number(lpl * jdl * ((res.rows[i].actualQty / res.rows[i].planQty) * 100) / 10000).toFixed(2)));
          //       jhArr.push(res.rows[i].planQty);
