    getTotalTime() {
      let time = 0;
      switch (this.searchData.timeKeyEnum) {
        case 'LESSON':
          time = 2 * 60;
          break;
        case 'DAY':
          time = 24 * 60;
          break;
        case 'MONTH':
          time = 365 * 24 * 60;
          break;
      }
      return time;
    },
    // 计算性能稼动率
    getPerformanceRate() {
      const { planCt, actualQty, runTime } = this.currentIndexInfo;
      return ((planCt * actualQty) / (runTime * 60));
    },
    // 计算OEE
    getOeeRate() {
      return (this.currentIndexInfo.yield * this.currentIndexInfo.availability * this.getPerformanceRate() / 100).toFixed(2);
    },
