              }
            },
            label: {
              show: true,
              position: [barWidth / 2, -(barWidth - 10)],
              color: '#ffffff',
              fontSize: 14,
              fontStyle: 'bold',
              align: 'center',
              formatter: params => {
                return params.value;
              }
            },
            data: yList
          }
        ]
      };
      lineChart.setOption(option, true);
      window.addEventListener('resize', () => {
        lineChart.resize();
      });
    }
  }
};
